import './UserPostCard.scss'
import UserPostImg from '../../assets/images/post-card-img.png'
import DotsImg from '../../assets/images/other-menu-dots.svg'

const UserPostCard = () => {
    return (
        <>
            <div className="user-post-card">
                <div className="top-wrapper">
                    <div className="user-post-card-left">
                        <img src={UserPostImg} alt="" />
                        <h3>Срочно нужен программист yii2</h3>
                    </div>
                    <div className="user-post-card-right">
                        <div className="box">
                            <ul>
                                <li>2 000 000</li>
                                <li>6 000 000 сум</li>
                            </ul>
                        </div>
                        <div className="box">
                            <ul>
                                <li># 345 002</li>
                                <li>С: 28 июнь</li>
                                <li>По: 28 июль</li>
                            </ul>
                        </div>
                        <div className="box">
                            <button className="add ">Поднять</button>
                            <button className="add blue">Рекламировать</button>
                            <button className='other-menu'>
                                <img src={DotsImg} alt="" />
                            </button>
                        </div>
                    </div>
                </div>
                <div className="user-post-card-bottom">
                    <ul>
                        <li className='active' >Статистика</li>
                        <li>Просмотры: 195</li>
                        <li>Тел.: 200</li>
                        <li>В Избранном: 0</li>
                    </ul>
                </div>
            </div>
        </>
    )
}

export default UserPostCard